.mainNav{
    position:-webkit-sticky;
}

.mainNav nav{
    position: relative;
    margin-top:100vh;
    width: 100%;
    padding: 2rem;
    background-color: #fff;
    box-shadow: 0px 10px 10px -5px #aaa; 
}

.mainNav nav button{
    position: absolute;
    top: 15px;
    right: 10px;
    border-style:none;
    background: none;
}

.mainNav nav button i{
    font-size: 4rem;
}

.mainNav nav ul{
    overflow: hidden;
    transition: all 1s;
}

.mainNav nav ul li{
    display: block;
    padding:5px 5px 15px 5px;
    list-style: none;
    text-align: center;
}

.mainNav a{
    color: #000;
    font-size: 2.5rem;
    letter-spacing: 1px;
    font-weight: bold;
    text-decoration: none;
}

.mainNav a:hover{
    color:rgb(74, 188, 221);
}
