* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 10px;
  font-family: 'Lato', sans-serif;
}

html{
    font-family: 'Lato', sans-serif;
}

.App{
  max-width: 1200px;
  margin: 0 auto;
  /* height: 200vh; */
}

.forAll{
  background-color: #fff;
  padding: 15px;
}

.forAll h1{
  padding: 20px 0;
  font-size: 3rem;
  text-align: center;

}

.forAll h1::after{
  content:"";
  display: block;
  width: 50%;
  margin: 4px auto;
  border-bottom: 3px solid #4ABCDD;
}

@media(min-width:600px){
  .forAll{
    padding: 45px 45px;
  }

  .forAll h1{
    padding: 20px 0 150px 0;
    font-size: 4rem;
  }
}

@media(min-width:1400px){
  .App{
    max-width: 1600px;
  }
}


header{
    position: fixed;
    top:0;
    left:0;
    height: 100vh;
    width: 100%;
    background-image: url(/static/media/progra.9efcd059.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: -70px 0px;
    z-index: -1;

}

.bgcOpacity{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: #000;
    opacity: 0.6;
    
}

.title{
    position: absolute;
    width: 100%;
    text-align: center;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50% , -10%);
            transform: translate(-50% , -10%);

    color: white;
    letter-spacing: 2px;
    line-height: 5rem;
}

.title h1{
    font-size: 3.5rem;
    padding-bottom: 30px;
    text-shadow: 2px 2px 3px rgba(0,0,0,0.3);
}

.title h3{
    font-size: 2rem;
    text-shadow: 2px 2px 3px rgba(0,0,0,0.3);
}

/* CSS for screen bigger then 600px */

@media(min-width:576px){

    /* background position */
    header{
        background-position:0px 0px;
       }

    /* .title{
        transform: translate(-40% , -10%);
    } */

    .title h1{
        font-size: 6rem;
        padding-bottom: 70px;
    }
    
    .title h3{
        font-size: 3rem;
    }
}

@media(min-width:800px){
    .title{
        -webkit-transform: translate(-40% , -10%);
                transform: translate(-40% , -10%);
}
}

.mainNav{
    position:-webkit-sticky;
}

.mainNav nav{
    position: relative;
    margin-top:100vh;
    width: 100%;
    padding: 2rem;
    background-color: #fff;
    box-shadow: 0px 10px 10px -5px #aaa; 
}

.mainNav nav button{
    position: absolute;
    top: 15px;
    right: 10px;
    border-style:none;
    background: none;
}

.mainNav nav button i{
    font-size: 4rem;
}

.mainNav nav ul{
    overflow: hidden;
    -webkit-transition: all 1s;
    transition: all 1s;
}

.mainNav nav ul li{
    display: block;
    padding:5px 5px 15px 5px;
    list-style: none;
    text-align: center;
}

.mainNav a{
    color: #000;
    font-size: 2.5rem;
    letter-spacing: 1px;
    font-weight: bold;
    text-decoration: none;
}

.mainNav a:hover{
    color:rgb(74, 188, 221);
}

