@media(min-width:576px){
    .line{
        display: inline;
    }

    .leftTimeLine .schoolName::after{
        content:"";
        display: block;
        position: absolute;
        top:50%;
        right:-10px;
        height: 16px;
        width: 16px;
        margin-left: 20px;
        background-color: #fff;
        border: 2px solid #555;
        border-radius: 100px;
    }
    
    .rightTimeLine .schoolName::before{
        content:"";
        display: block;
        position: absolute;
        top:50%;
        left:-10px;
        height: 16px;
        width: 16px;
        margin-right: 20px;
        background-color: #fff;
        border: 2px solid #555;
        border-radius: 100px;
    }

    .leftTimeLine{
        padding-right: 50%;
    }
    
    .rightTimeLine{
        padding-left: 50%;
    }

    .school{
        padding: 0 40px;
    }

    .education p{
        padding-bottom: 50px;
    }
  }
