.item{
    position: relative;
    overflow: hidden;
    margin: 40px 0;
    box-shadow: 0px 0px 10px #777;
}

/* ready for destktop */

.itemCoverOpacity{
    position: absolute;
    height: 99.5%;
    width: 100%;
    top: 0;
    left: 100%;
    background-color: rgba(74, 188, 221, 0.9);

    -webkit-transition: left 1.2s;

    transition: left 1.2s;
}

.itemCoverContent{
    position: absolute;
    top: 10%;
    left:100%;
    width: 90%;
    /* padding:1% 1% 5% 0; */
    border-bottom: 2px solid white;
    text-decoration: none;

    -webkit-transition: left 1.5s;

    transition: left 1.5s;
}

.itemCoverContent h2{
    padding-bottom: 20px;
    font-size: 3.5rem;
    color: #fff;
}

.itemCoverContent p{
    font-size: 1.2rem;
    padding:0 20px 10px 0;
    color: #fff;
}

.item:hover .itemCoverOpacity{
    left: 0%;
}

.item:hover .itemCoverContent{
    left:10%;
}

.touchButton{
    position: absolute;
    bottom:20px;
    right:20px;
}

.touchButton i{
    font-size: 5rem;
    color: rgba(20, 20, 20, 0.5);
}

@media(min-width:576px){
    .touchButton{
        display: none;
    }

    .itemCoverContent h2{
        padding-bottom: 70px;
        font-size: 5.5rem;
    }

    .itemCoverContent p{
        font-size: 2rem;
        padding:40px 20px 10px 0;
        color: #fff;
    }
}


@media(min-width:992px){
    .item{
        margin: 60px auto;
        max-width: 800px;
    }

}


.contacts{
    padding-top:60px;
}

.contact{
    position: relative;
    display: -webkit-flex;
    display: flex;
}

.contact a {
    height: 100px;
    width: 150px;
}

.contact a i{
    position: relative;
    top: 50%;
    left: 50%;

    -webkit-transform: translate(-50% , -50%);

            transform: translate(-50% , -50%);

    font-size: 7rem;
    color: #000;
}

/* for desktop */

.contact a i:hover{
    /* font-size: 7rem; */
    color: rgba(74, 188, 221, 0.9);
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.text{
    display: -webkit-flex;
    display: flex;
    width: 100%;   
    -webkit-align-items: center;   
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;    
}

.text p{
    font-size: 1.2rem;
    letter-spacing: 1px;
}

@media(min-width:768px){
    .contact a i{
        font-size:5rem;
    }
    .contact a i:hover{
        font-size: 7rem;
